.payment-main {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-color: #121212;
  z-index: -2;
}

.geometric-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  pointer-events: none;
  filter: blur(2px);
}

.gradient-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  background: linear-gradient(to bottom, #391d55 10%, transparent 90%);
}

.cards-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-card {
  background-color: #121212;
  color: #fff;
}